<template>
  <div>
    <el-row>
      <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
        <h1 class="pageTitle">{{ this.pageTitle }}</h1>
      </el-col>

      <el-col id="listData" :xs="24" :sm="24" :md="24" :lg="24" :xl="24" style="background: #fff;">
        <el-row>
          <el-col :span="24">
            <el-form ref="form" :model="form" label-width="160px">
              <h3>Configurações da Conta</h3>
              <el-form-item label="URL da Conta">
                <el-input v-model="form.url"></el-input>
              </el-form-item>

              <el-form-item label="Icone da Empresa">
                <el-input v-model="form.icone"></el-input>
              </el-form-item>

              <el-form-item label="Logo da Empresa">
                <el-input v-model="form.logo"></el-input>
              </el-form-item>

              <el-form-item label="Nome da Empresa">
                <el-input v-model="form.nome_empresa"></el-input>
              </el-form-item>

              <el-divider></el-divider>
              <h3>Dados da Empresa</h3>

              <el-form-item label="Razão Social">
                <el-input v-model="form.razao_social"></el-input>
              </el-form-item>

              <el-form-item label="CNPJ">
                <el-input v-model="form.cnpj"></el-input>
              </el-form-item>

              <el-form-item label="CEP">
                <el-input v-model="form.cep"></el-input>
              </el-form-item>

              <el-form-item label="Rua">
                <el-input v-model="form.rua"></el-input>
              </el-form-item>

              <el-form-item label="Número">
                <el-input v-model="form.numero"></el-input>
              </el-form-item>

              <el-form-item label="Bairro">
                <el-input v-model="form.bairro"></el-input>
              </el-form-item>

              <el-form-item label="Cidade">
                <el-input v-model="form.cidade"></el-input>
              </el-form-item>

              <el-form-item label="Estado">
                <el-input v-model="form.estado"></el-input>
              </el-form-item>

              <el-form-item label="País">
                <el-input v-model="form.pais"></el-input>
              </el-form-item>

              <el-divider></el-divider>
              <h3>Informações de Contato</h3>

              <el-form-item label="Usuário Principal">
                <el-input v-model="form.usuario_admin"></el-input>
              </el-form-item>

              <el-form-item label="E-mail de Faturamento">
                <el-input v-model="form.email_financeiro"></el-input>
              </el-form-item>

              <el-form-item label="E-mail de Contato">
                <el-input v-model="form.email_contato"></el-input>
              </el-form-item>

              <el-form-item label="Telefone">
                <el-input v-model="form.telefone"></el-input>
              </el-form-item>

              <el-form-item label="Celular">
                <el-input v-model="form.celular"></el-input>
              </el-form-item>

              <el-form-item label="Nome do Contato Principal">
                <el-input v-model="form.nome_contato"></el-input>
              </el-form-item>

              <el-form-item>
                <el-button type="primary" @click="onSubmit">Salvar</el-button>
                <el-button>Cancelar</el-button>
              </el-form-item>
            </el-form>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "AccountDetails",
  data() {
    return {
      loading: false,
      pageTitle: "Conta",
      form: {
        url: "",
        icone: "",
        logo: "",
        nome_empresa: "",
        razao_social: "",
        cnpj: "",
        cep: "",
        rua: "",
        numero: "",
        bairro: "",
        cidade: "",
        estado: "",
        pais: "",
        usuario_admin: "",
        email_financeiro: "",
        email_contato: "",
        telefone: "",
        celular: "",
        nome_contato: "",
      },
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      this.$http.get("account/details").then((res) => {
        res;

        this.form.url = res.data.account_domain;
        this.form.nome_empresa = res.data.account_company_name;
        this.form.razao_social = res.data.account_razao_social;
        this.form.cnpj = res.data.account_cnpj;
        this.form.cep = "";
        this.form.rua = res.data.account_address;
        this.form.numero = res.data.account_adress_number;
        this.form.bairro = res.data.acount_district;
        this.form.cidade = res.data.account_city;
        this.form.estado = res.data.account_state;
        this.form.pais = res.data.account_country;
        this.form.usuario_admin = res.data.account_email_create;
        this.form.email_financeiro = res.data.account_email_financial;
        this.form.email_contato = res.data.account_email_contact;
        this.form.telefone = res.data.account_phone;
        this.form.celular = res.data.account_cellphone;
        this.form.nome_contato = res.data.account_contact_name;
      });
    },
    updateData() {
      this.$http.post("account/details", this.form).then((res) => {
        res;
        this.getData();
      });
    },
    onSubmit() {
      this.updateData();
    },
  },
  head: {
    title: {
      inner: "Conta | Detalhes",
      complement: "GuiaSales",
    },
  },
};
</script>

